
import Link from 'next/link';
import Image from 'next/image';
import type { EventData } from '@/lib/types';
import { Card, CardContent, CardFooter, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { CalendarDays, MapPin, Ticket, Users, DollarSign, Building, Home } from 'lucide-react'; // Added Home icon
import { cn } from '@/lib/utils';
import { Badge } from '@/components/ui/badge';
import * as React from 'react';


interface EventCardProps {
  event: EventData;
  isFeatured?: boolean;
}

export default function EventCard({ event, isFeatured = false }: EventCardProps) {
  const [formattedDate, setFormattedDate] = React.useState('');
  const [formattedTime, setFormattedTime] = React.useState('');

  React.useEffect(() => {
    if (event.data_horario) {
      const eventDate = new Date(event.data_horario);
      const timeZone = 'America/Sao_Paulo'; 

      setFormattedDate(eventDate.toLocaleDateString('pt-BR', {
        day: '2-digit',
        month: 'long',
        year: 'numeric',
        timeZone,
      }));
      setFormattedTime(eventDate.toLocaleTimeString('pt-BR', {
        hour: '2-digit',
        minute: '2-digit',
        timeZone,
      }));
    }
  }, [event.data_horario]);

  if (isFeatured) {
    const backgroundImageUrl = event.imagem_url || `https://picsum.photos/seed/${event.id}/1200/800`;
    return (
      <div 
        className="relative w-full text-white h-[60vh] sm:h-[70vh] md:h-[80vh]" 
        style={{
          backgroundImage: `url(${backgroundImageUrl})`,
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center center',
          backgroundSize: 'cover',
        }}
        data-ai-hint="event hero background"
      >
        <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/40 to-transparent z-10" /> 
        <div className="relative z-20 flex flex-col justify-end h-full p-6 sm:p-8 md:p-12 lg:p-16">
          <h2 className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold mb-2 drop-shadow-lg">{event.nome_evento}</h2>
          <p className="text-md sm:text-lg md:text-xl mb-1 drop-shadow-md"><CalendarDays className="inline mr-2 h-5 w-5" />{formattedDate} às {formattedTime}</p>
          {event.nome_local_evento && <p className="text-md sm:text-lg md:text-xl mb-1 drop-shadow-md"><Home className="inline mr-2 h-5 w-5" />{event.nome_local_evento}</p>}
          <p className="text-md sm:text-lg md:text-xl mb-1 drop-shadow-md"><MapPin className="inline mr-2 h-5 w-5" />{event.local}</p>
          <p className="text-md sm:text-lg md:text-xl mb-4 drop-shadow-md"><Building className="inline mr-2 h-5 w-5" />{event.cidade}</p>
          <Button asChild size="lg" className="bg-accent hover:bg-accent/80 text-accent-foreground font-semibold w-full max-w-xs sm:w-auto self-start text-base md:text-lg py-3 px-6">
            <Link href={`/event/${event.id}`}>
              <Ticket className="mr-2 h-5 w-5" />
              Ver Detalhes
            </Link>
          </Button>
        </div>
      </div>
    );
  }

  return (
    <Card className={cn(
      "flex flex-col overflow-hidden h-full shadow-lg hover:shadow-xl transition-shadow duration-300 relative"
    )}>
      <CardHeader className="p-0">
        <div className="relative h-48 w-full">
          <Image
            src={event.imagem_url || `https://picsum.photos/seed/${event.id}/600/400`}
            alt={event.nome_evento}
            layout="fill"
            objectFit="cover"
            data-ai-hint="event concert festival"
          />
        </div>
        <div className="p-6">
          <CardTitle className="text-2xl font-semibold text-primary">{event.nome_evento}</CardTitle>
          <CardDescription className="mt-1 text-sm text-muted-foreground h-20 overflow-hidden text-ellipsis">
            {event.descricao}
          </CardDescription>
        </div>
      </CardHeader>
      <CardContent className="flex-grow space-y-3 px-6 pb-4">
        <div className="flex items-center text-sm text-foreground">
          <CalendarDays className="mr-2 h-5 w-5 text-primary" />
          <span>{formattedDate} às {formattedTime}</span>
        </div>
        {event.nome_local_evento && (
          <div className="flex items-center text-sm text-foreground">
            <Home className="mr-2 h-5 w-5 text-primary" />
            <span>{event.nome_local_evento}</span>
          </div>
        )}
        <div className="flex items-center text-sm text-foreground">
          <MapPin className="mr-2 h-5 w-5 text-primary" />
          <span>{event.local}</span>
        </div>
        <div className="flex items-center text-sm text-foreground">
          <Building className="mr-2 h-5 w-5 text-primary" />
          <span>{event.cidade}</span>
        </div>
        <div className="flex items-center text-sm text-foreground">
          <DollarSign className="mr-2 h-5 w-5 text-primary" />
          <span>R$ {event.preco_ingresso.toFixed(2).replace('.', ',')}</span>
        </div>
        <div className="flex items-center text-sm text-foreground">
          <Users className="mr-2 h-5 w-5 text-primary" />
          <span>{event.quantidade_disponivel} ingressos disponíveis</span>
        </div>
      </CardContent>
      <CardFooter className="px-6 pb-6">
        <Button asChild className="w-full bg-accent hover:bg-accent/90 text-accent-foreground font-semibold">
          <Link href={`/event/${event.id}`}>
            <Ticket className="mr-2 h-5 w-5" />
            Comprar Ingresso
          </Link>
        </Button>
      </CardFooter>
    </Card>
  );
}
